import React, {  useEffect} from 'react'
import './home.css'
import { Container,Tabs, TabList, TabPanels, Tab, TabPanel} from '@chakra-ui/react'
import { Image } from '@chakra-ui/react'
import Login from "../component/authenticate/log";
import Signup from "../component/authenticate/sign";
import { useHistory } from 'react-router-dom'


const Homepage = () => {
   const history =useHistory();
    useEffect(()=>{
        const user =JSON.parse(localStorage.getItem("userInfo"));
      

        if(user){
            history.push("/chats");
        }

    },[history]);
    
  return (
    <Container className='contain'>
    
    
      {/* <Text  class="text">Chit-Chat</Text> */}
    <Image  className='img' src="data:image/png;base64," />
    <Tabs className="tab">
  <TabList>
    <Tab  className='log'>Login</Tab>
    <Tab className='sign'> Signup</Tab>
  </TabList>

  <TabPanels>
    <TabPanel>
    <Login/>
    </TabPanel>
    <TabPanel>
      <Signup/>
    </TabPanel>
    
  </TabPanels>
</Tabs>
    </Container>
  )
}

export default Homepage
