import React from 'react'
// import ScrollableFeed from 'react-scrollable-feed'
import { isLastMessage, isSameSender, isSameSenderMargin, isSameUser } from '../config2/chatLogic'
import { Avatar, Box, Tooltip } from '@chakra-ui/react';
import {ChatState} from '../Context/ChatProvider'
const ScrollableChat = ({messages}) => {

  const {user} =ChatState();
  return (
     <Box>
        {messages && messages.map((m,i)=>(
          <div style={{display:"flex"}} key={m._id}>
             {
              (isSameSender(messages,m,i,user._id)
              || isLastMessage(messages,i,user._id)) && (
              <Tooltip
              label={m.sender.name}
              placement='bottom-start'
              hasArrow>
       <Avatar marginRight='15px'  marginTop='5px' cursor="pointer" size="md" name={m.sender.name} src={m.sender.pic} />
              </Tooltip>
            ) }
            <span
              style={{
      backgroundColor:`${m.sender._id ===user._id ? "rgb(0,0,0)" : "rgb(236, 90, 11, 0.874)"}`,
      borderRadius:"20px",
      color:'white',
      paddingRight:"15px",
      paddingLeft:"15px",
      paddingTop:"10px",
      paddingBottom:"10px",
      marginBottom:"10px",
      maxWidth:"75%",
        marginLeft:isSameSenderMargin(messages,m,i,user._id),
      marginTop:isSameUser(messages,m,i,user._id) ? 3 : 10,
              }}>
             {m.content}   
            </span>
          </div>
        ))}
    </Box>
  )
}

export default ScrollableChat

